















import Vue from 'vue'
import SButton from '@/components/SButton.vue'

export default Vue.extend({
  name: 'SFilter',
  components: { SButton },
  data() {
    return { selected: 'timeDown', input: '' }
  },
  props: {
    filter: {
      type: Object,
      default() { return {} },
    },
    onConfirm: {
      type: Function,
      required: true,
    },
  },
  methods: {
    reset() {
      this.input = ''
      this.selected = 'timeDown'
      this.onConfirm(this.input, this.selected)
    },
  },
})
