












import Vue from 'vue'

export default Vue.extend({
  name: 'SCard',
  props: {
    header: {
      type: String,
      default() { return '' },
    },
    shadow: {
      type: String,
      default() { return 'always' },
      validator(value: string) { return ['always', 'none', 'hover'].includes(value) },
    },
    headerBackgroundColor: {
      type: String,
      default() { return '#01847F'},
    },
    headerColor: {
      type: String,
      default() { return '#fff' },
    },
    bodyStyle: {
      type: Object,
      default() { return { padding: '20px' }},
    },
  },
})
